body {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  margin-block-start: 0;
  margin-block-end: 0;
}

p {
  margin: 0;
}

h2 {
  margin: 0;
  margin-block-start: 1em;
  margin-block-end: 1em;
}

h3 {
  margin: 0;
  margin-block-start: 1em;
  margin-block-end: 1em;
}

a {
  margin: 0;
}

/* You should reset default margins for elements in
reset.css property and set only needed margins for elements */
